/*--------------------Bia-Formateurs-apropos--------------------------*/
body.formateurs,body.a-propos{
	main{
    	.row-title{
    		margin-top: 60px;
    		h4{
	    		font-family: druk-medium;
			    color: $biaRed;
			    font-size: 56px!important;
			    letter-spacing: 3px;
			    -webkit-transform: skew(180deg,167deg);
			    transform: skew(180deg,167deg);
			    /* position: absolute; */
			    text-transform: uppercase;
			    top: 50px;
			    z-index: 999;
			    margin-bottom: 50px;
			    padding-left: 5%;
    			margin-left:0px;
    		}
    	}

    	.texte-intro{
    		width:75%;
    		margin-left:auto;
    		margin-right:auto;
    		font-family: apercu;
   			font-size: 16px;
   		    -webkit-font-smoothing: antialiased;
   		    margin-bottom:60px;
    	}

    	.row-post{
    		background-color: white;
    		padding: 20px 15px 20px 15px;
    		margin-bottom: 25px;
    		font-family: apercu;
		    font-size: 15px;
		    outline: none;
		    .post-left{
		    	img{
		    		margin-bottom:20px;
		    	}
		    	.bouton{
		    		margin-left:auto;
		    		margin-right:auto;
		    	}
		    }
		    .post-right{
		    	padding-top: 25px;
			    padding-bottom: 25px;
			    padding-left: 50px;
			    padding-right: 15px;
			    .content{    
			    	width: 95%;
			    }
		    	h4{
			    	font-family: druk-medium;
				    color: #000;
				    font-size: 36px!important;
				    letter-spacing: 2px;
				    -webkit-transform: skew(180deg,167deg);
				    transform: skew(180deg,167deg);
				    text-transform: uppercase;
				    width: 220px;
				    top: 50px;
				    z-index: 999;
				    margin-bottom: 42px;}
		    	}
		    .contenu{
		    	width: 100%;
    			margin-bottom: 25px;
    			-webkit-font-smoothing: antialiased;
			    transform: translateZ(0);
			    overflow:hidden;
			    position:relative;
			    
			    
		    }

		    .post-in-list{
		    	display:inline-block;
		    	vertical-align:middle;
		    	height:auto;
		    }

		    .post-in-list:last-of-type {
    			padding-left: 4%;
    			.text-section{
    				transition:0.5s height;
				    	overflow:hidden;
				    	position:relative;
				    	padding-top:10px;

					&.max-height{
				    	height:250px;

				    	&:before{
				    		content:'';
				    		width:100%;
				    		height:50px;
				    		/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#ffffff+0,ffffff+100&0+0,1+100 */
							background: -moz-linear-gradient(top,  rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%); /* FF3.6-15 */
							background: -webkit-linear-gradient(top,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%); /* Chrome10-25,Safari5.1-6 */
							background: linear-gradient(to bottom,  rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
							filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=0 ); /* IE6-9 */
				    		position:absolute;
				    		bottom:0;
				    		z-index:5;
				    	}
				    }
				}
			}



		    .formateurs-link{
		    	display: block;
			    width: auto;
			    margin-left: auto;
			    margin-right: auto;
			    margin-top: 20px;
    			margin-bottom: 20px;
    			text-align: center;
			    a{
				    text-align: center;
				    
			    }
			    .bouton{
			    	display:table;
			    	margin-right: auto;
			    	margin-left: auto;
			    	margin-bottom: 10px;

			    	}
		    }
		    
		     .bouton{
			    	display:table;
			    	
			    	margin-bottom: 10px;

			    	}


    		img{
			    background-size: 100%;
			    border-radius: 50%;
			    position: relative;
			    height: 300px;
			    width: 300px;
			    z-index: 4;
			    margin-left: auto;
			    margin-right: auto;
			    display: block;
    		}
    		h5{
    			font-family: druk-medium;
			    color: #000;
			    font-size: 36px!important;
			    letter-spacing: 2px;
			    transform: skew(180deg,167deg) translateZ(0);
			    text-transform: uppercase;
			    width: 220px;
			    top: 50px;
			    z-index: 999;
			    margin-bottom: 42px;
    		}
    		.titre{
    			font-family: apercu;
    			font-size: 18px;
    			outline: none;
    			font-weight:bold;
    			margin-bottom:30px;
    		}
    	}
    	
	}
	@media only screen and (max-width: 768px) {
	    main{
	    	.row-post{
	    		.formateurs-link{
	    			margin-bottom:45px;
	    		}

	    		.post-right{
	    			padding:25px 15px;
	    		}

	    			img{
	    				height:auto;
	    			}
	    	}

	    	.row-title{
	    		h4{
	    			width:290px;
	    		}
	    	}

	    	.texte-intro{
	    		width:100%;
	    		margin-left:5%;
	    		margin-right:5%;
	    		padding-right:30px;
	    	}

	    	.bouton{
	    		margin-top:35px;
	    	}

	    }
	}
}

body.a-propos{
	main{
		.row-title .texte-intro{
			h4{	
				margin-left:0;
				padding-left:0;
			}
		}
	}

}
