body.woocommerce-page{
	.page-header{
		display:none;
	}

	main{
		padding:70px 5% 0;
	}

	.woocommerce{
		.titreSection{
			display:table;
			position:relative;
			margin-bottom: 0;
	    	top: 25px;
	    	left:25px;
		}
	}

	.checkout-button{
		font-size:14px!important;
	}

	.create-account{
		display:none;
	}

	.woocommerce-shipping-fields{
		.titreSection{
			top:15px;
		}
	}

	#order_comments{
		min-height:200px;
	}
}

body.woocommerce-checkout{

	.club_bia_ajax_checkbox_container {
		label.checkbox {
			display: inline-block;
		}
	}
	#splash{
		height:80px;
		min-height:initial;
	}

	#barreV{
		display:none;
	}

	#allergies-section,
	.club_bia_ajax_checkbox_container{
		display:table;
		margin-top:30px;
		width:100%;
		background-color: #f2f2f2;
		padding:15px 25px 20px;

		h4{
			display:block;
			clear:both;	
			font-family: apercu;
    		font-size: 16px;
		}

		.checkbox-container{
			margin-left:-15px;
			margin-right:-15px;
			display:flex;
			flex-wrap:wrap;
		}

		.allergies-checkbox{
			width:33.33%;
			padding:0 15px;
			display:flex;
			flex-wrap:wrap;
		}

	}

	.woocommerce-info{
		display:none;
	}

	.allergies-checkbox{
		max-width:33.33%;

	}

	.comments-section{
		margin-top:30px;
		margin-bottom:30px;
		textarea{
			min-height:150px;
		}
	}

	.woocommerce-billing-fields{
		label{
			display:none;
		}
	}

	#billing_company_field{
		display:none;
	}

	.select2-container .select2-choice{
		border:0;
		border-bottom:3px solid black;
		text-align:center;
		margin-bottom:15px;
	}

	.select2-search{
		input[type="text"]{
			border:0;
		}
	}

	.woocommerce-shipping-fields,
	.club_bia_ajax_checkbox_container{		

		label.checkbox{
			margin-top:0;

			#photo{
				margin-left:0;
			}
		}

		.auth-photo{
			margin-top:10px;
		}

		.newsletter{
			margin-left: -5px;
		    margin-top: 10px;
		    border-top: 1px solid lightgray;
		    padding-top: 10px;
		    margin-bottom: 0;
		}

		input[type="checkbox"]{
			margin:0 5px 0;
			position:relative;
			-webkit-appearance: none;
		    -moz-appearance: none;
		    appearance: none;
		    border:0;
		    display: block;
    		float: left;


			&:before{
				content:'';
				width:15px;
				height:15px;
				display:table;
				border-radius:0px;
				border:3px solid $biaRed;
				background-color:white;
				transition:0.3s all ease-in-out;
				top: 10px;
    			position: relative;
			}

			&:checked{
				&:before{				
					border:1px solid transparent;
					background-color:$biaRed;
				}
			}
		}
	}

	.woocommerce-billing-fields{

		input[type="text"],input[type="email"],input[type="tel"]{
			display:table;
			margin-bottom:15px!important;
		}

	}

	#payment{
		margin-top: 25px;
	    border-radius: 0;
	    padding: 25px;
	    background-color:#f2f2f2;
		div.form-row{
			margin:0;
		}

		div{
			&.form-row{
				margin:0;
			}
			&.payment_box{
				background-color:white;
				&:before{
					border:1em solid #fff;
					border-right-color: transparent;
				    border-left-color: transparent;
				    border-top-color: transparent;
				}
			}
		}
	}
}

body.woocommerce-cart{
	#splash{
		height:80px;
		min-height:initial;
	}

	#barreV{
		display:none;
	}

	.cart-empty{
		margin-bottom:30px;
	}

	.woocommerce .cart-collaterals .cart_totals, .woocommerce-page .cart-collaterals .cart_totals{
		width:33.33%;
	}

	.coupon{
		float:right!important;
		margin-top:45px;
		.input-text{
			width:auto!important;
			text-align:center;
		}
		&:before{
			content: "";
		    display: table;
		    position: relative;
		    top: 40px;
		    left: 0;
		    width: 0;
		    height: 0;
		    z-index: 9;
		    border-top: 20px solid #fff;
		    border-right: 20px solid transparent;
		}	
	}

	table.cart td.actions .coupon .input-text{
		border:none;
		border-bottom:3px solid black;
		padding:7px 6px;
		margin-right:15px;
		z-index:9;
		position:relative;
	}

	#update_cart_section{
		display:table;
		position:relative;
		&:after{
    		content: "";
		    display: table;
		    position: absolute;
		    bottom: 15px;
		    left: 0;
		    width: 0;
		    height: 0;
		    z-index: 9;
		    border-top: 20px solid white;
		    border-right: 20px solid transparent;
    	}
	}

	input[name="update_cart"]{
		margin-top: 65px;
    	margin-right: 45px;
    	
	}

	.product-thumbnail{
		.circle{
			width: 70px;
		    height: 70px;
		    background-size: cover;
		    background-position: center center;
		    border-radius: 200%;
		    box-shadow: 0 1px 5px #888;
		    margin-bottom:15px;
		    margin-top:15px;
		}
	}

	.coupon{
		.triangleRouge{
			position:relative;
			&:before{
				content: "";
			    display: table;
			    position: absolute;
			    top: -8px;
			    left: 0;
			    width: 0;
			    height: 0;
			    z-index: 9;
			    border-top: 20px solid #fff;
			    border-right: 20px solid transparent
			}
		}
	}
}

body.woocommerce-order-received{
	.thank-you-bloc{
		max-width: 800px;
	    margin:50px auto 60px;
	}

	.row-bottom{
		margin-top:100px;
	}

	.white-bloc{
		position:relative;
	}

	.woocommerce h3.titreSection{
		top:-50px!important;
		position:absolute!important;
	}

	.shop_table{
		margin-left:-12px;
	}

	.order_details{
		margin-top:60px;
	}

	.order_details-container{
		margin-bottom:50px;
	}

}

body.single-product{
	.product_meta, .quantity, .stock, .woocommerce-Price-amount{
		display:none;
	}

	button.button{
		float:right;
		display:block;
		margin-top:30px;
	}
}

.woocommerce{
	font-family:'apercu';

	table.shop_table{
		margin-bottom:0;
		border:0;
		th{
			font-family:'apercu';
			letter-spacing:0.05em;
		}
	}

	a.remove{
		color:$biaRed!important;
		font-family:'apercu';
		background-color:white;
		transition:0.3s all ease-in-out;
		&:hover{
			background-color:$biaRed;
		}
	}
}

.woocommerce-message{
	border-top-color:$biaRed!important;
	&:before{
		color:$biaRed;
	}
}

.woocommerce a.added_to_cart{
	position:absolute;
	right:45px;
	transform: translateY(-10px);
	bottom:60px
}

.cart_item{
	p{
		margin-bottom:0;
	}
}

#wc-stripe-cc-form{
	input{
		width: 100%!important;
    	text-align: left;
	}
}

@media only screen and (max-width: 767px) {
	#wc-stripe-cc-form{
		.form-row{
			width:100%!important;
		}
	}
}

.quantity{
	input{
		border: 0;
	    border-bottom: 3px solid black;
	    text-align: center;
	    padding: 3px 3px 3px 15px;

	    &:hover, &:focus{
	    	outline:none;
	    }
	}
}

.wc_payment_method{
	input[type="radio"]{
		display:none;
		&:checked + label{
			&:before{				
				border:1px solid white;
				background-color:$biaRed;
			}
		}
	}
	>label{
		position:relative;
		padding-left:25px;
		&:before{
			content:'';
			width:15px;
			height:15px;
			display:table;
			border-radius:200%;
			border:3px solid $biaRed;
			background-color:white;
			transition:0.3s all ease-in-out;
			position:absolute;
			top: 50%;
			left:0;
    		transform: translateY(-50%);
		}
	}

	img{
		mix-blend-mode: darken;
	}
}

.place-order{
	.triangleRouge{
		display:table;
		float:right;
		&:before{
			content: "";
		    display: table;
		    position: relative;
		    top: 20px;
		    left: 0;
		    width: 0;
		    height: 0;
		    z-index: 9;
		    border-top: 20px solid #f2f2f2;
		    border-right: 20px solid transparent;
		}
	}
}


.white-bloc{
	padding:65px 25px 25px;
	background-color:white;
	box-shadow: 3px 2px 12px rgba(0,0,0,.14);
}

@media only screen and (max-width: 580px) {
	.woocommerce table.shop_table{
		margin-left:-20px;
	}

	body.woocommerce-page{
 		.titreSection{
			font-size:40px!important;
		 }
	}
}

@media only screen and (max-width: 1300px) {
	body.woocommerce-checkout{
		 #allergies-section{
		 	 .allergies-checkbox{
		 	 	width:50%;
		 	 	max-width:50%;
		 	 }
		 }

	}
}

@media only screen and (max-width: 860px) {
	body.woocommerce-checkout{
		 #allergies-section{
		 	 .allergies-checkbox{
		 	 	width:100%;
		 	 	max-width:100%;
		 	 }
		 }

	}
}

@media only screen and (max-width: 768px) {
	body.woocommerce-cart{
		.coupon{
			float:none!important;
			width:100%;
			margin-bottom:30px;
			margin-top:0;
			&:before{
				display:none;
			}

			.input-text{
				width: 100%!important;
    			margin-bottom: 15px!important;
			}

			input[name="apply_coupon"]{
				width:100%!important;
			}

			.triangleRouge{
				&:before{
					display:none;
				}
			}
		}
	}

	body.woocommerce-checkout{
		 #allergies-section{
		 	 .allergies-checkbox{
		 	 	width:100%;
		 	 	max-width:100%;
		 	 }
		 }

		#size_shirt_field{
			width:100%;
		}
	}

	.woocommerce{
		 a.button.alt{
		 	font-size:1em;
		 }
	}

}
@media only screen and (max-width: 1440px) {
	body.woocommerce-cart{
		.woocommerce-page, .woocommerce{
			.cart-collaterals{
				.cart_totals{
					width:100%;
					th{
						width:50%;
					}

					td{
						text-align:right;
					}
				}
			}
		}
	}

}